

import { mapState, mapActions, mapMutations } from 'vuex'
import luckyItemApis from './../store/apis/luckyItem'
import { goDetails } from '../utils/hexiao'
import { partners } from './../config/variables'
import { ossPrefix, activityOssPrefix, santaPicture as santa, isDuringActivity } from './../config'
import { couponTypeFilter } from './../config/constant'
import { session, local } from './../utils/storage'
import commonApis from './../store/apis/common'
import sessionMenagement from './../utils/StorageManagement'
import Wechat from './../utils/wechat'
import report from './../utils/statistics'

import Toast from 'vux/src/components/toast/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import ConfigPlugin from 'vux/src/plugins/config/index.js'

export default {
directives: {
TransferDom
},
components: {
Flexbox,
FlexboxItem,
Divider,
XButton,
Confirm,
Grid,
GridItem,
XDialog,
Loading,
Toast
},
data() {
return {
activityOssPrefix,
santa: santa,
ossPrefix,
partners,
wish: false,//祝福弹窗
share: false,//分享图片显示
toastType: '',//提示类型
toastText: '',//提示文字
showToast: false,//提示信息
open_id: '',
show: false,
sorryText: '',
brief: false,
briefTitle: '和+消费共享平台简介',
cardList: [],//抽奖列表
index:1,//抽奖背景
dialog: false,
win:{},
source:'',
disClick:false,
choosed: '', // 抽奖动画时选中的项目
choosedItem: '', // 中奖项目
turningList: [], // 将项目的id按顺序排列
isDrawing: false,
choosedIndex: '',
stopClock: false,
timer: {},
showSanta: false,
isDuringActivity
}
},
beforeRouteLeave (to, from, next) {
if (this.isDrawing) {
next(false)
} else {
next()
}
},
methods: {
...mapActions({
getLotteryCount: 'getLotteryCount',
drawLottery: 'drawLottery',
getMyCoupons: 'getMyCoupons'
}),
...mapMutations({
setSantaDialog: 'setSantaDialog'
}),
getMoreChange () {
if (this.isDrawing) {
return
}
const targetPath = isDuringActivity ? '/taskBoard' : '/blessing'

this.$router.push(targetPath)
},
closeDialog () {
this.dialog = false
this.show = false
this.share = false
this.choosed = ''
},
getLuckyItems () {
luckyItemApis.list().then(res => {
this.cardList = res.data.items
this.cardList.forEach((item, index) => {
switch (index) {
case 6:
this.turningList[3] = item.id
break
case 5:
this.turningList[4] = item.id
break
case 4:
this.turningList[5] = item.id
break
case 3:
this.turningList[6] = item.id
break
default:
this.turningList[index] = item.id
break
}
})
console.log(this.turningList)
}).catch((err) => {
this.$vux.toast.show('获取奖品出错')
console.log('ERROR MESSAGE: ', err)
})
},
toWish() {
if (!this.myInfo.phone) {
this.$router.push({path: '/bindMobile', query: {type: 4}});
} else {
this.$router.push('/taskBoard')
}
},
toUse() {
local.save(local.KEY_TEMP_COUPON_INFO, this.choosedItem.prize)
this.choosedItem.prize && report('首页去使用按钮', '点击', couponTypeFilter(this.choosedItem.prize.type))
if (!this.myInfo.phone) {
this.$router.push({path: '/bindMobile', query: {type: 0, data: this.win}});
} else {
goDetails(this.choosedItem.prize, 1, this.$router)
}
},
toSantaWish () {
this.showSanta = false
this.setSantaDialog(false)
this.$router.push('/santaWish')
local.save(local.KEY_USER_VISITED, true)
},
closeSataCard () {
this.setSantaDialog(false)
local.save(local.KEY_USER_VISITED, true)
this.showSanta = false
},
shareOther() {
this.share = true;
console.log('分享给朋友');
},

toCoupons() {
if (!this.myInfo.phone) {
this.$router.push({path: '/bindMobile', query: {type: 2}});
} else {
this.$router.push('/coupons')
}
},
onConfirm(msg) {
},

onItemClick(name) {
if (name === 'us') {
report('品牌联盟链接', '跳转', `联系我们`)
this.brief = true
} else {
report('品牌联盟链接', '跳转', `${name}`)
}
},

lotteryTime() {
let date = new Date().getTime();
let start = new Date(this.cardList[0].start_time).getTime();
let end = new Date(this.cardList[0].end_time).getTime();
let could = true;
if (date >= start && date <= end) {
could = true;
} else if (date > end) {
could = false;
this.toastType = 'cancel';
this.toastText = '活动已结束！';
this.showToast = true;
} else if (date < start) {
this.toastType = 'cancel';
this.toastText = '活动还未开始！';
this.showToast = true;
could = false;
}
return could
},

async makeLottery () {
report('首页抽奖', '点击', '发起抽奖')
if (this.isDrawing) {
return
}
if (this.myCount <= 0) {
this.show = true;
this.share = false;
this.sorryText = '今天抽奖次数已用完！';
return
}
try {
this.isDrawing = true
let res = await this.drawLottery({ source: this.mySource })
if (res.code === 200 && res.data) {
report('首页抽奖', '点击', '成功');
let choosedIndex = this.turningList.indexOf(res.data.id)
this.choosedItem = res.data
this.beginAnimation(24 + choosedIndex + 1)
commonApis.behaveLogs(commonApis.BEHAVIOR_LOTTERY) 
} else {
report('首页抽奖', '点击', res.message);
this.$vux.toast.show({ text: res.message, type: 'cancel'})
this.isDrawing = false
}
} catch (err) {
this.isDrawing = false
this.disClick = false
this.toastType='cancel'
report('首页抽奖', '点击', '请求异常')
this.toastText='抽奖结果返回失败，请重试！'
this.showToast= true
this.stopClock = true
}
},
beginAnimation (num) {
let index = 0
let stepTime = 400
let gap = -70
let timer
let end = num
let setClock = () => {
timer = setInterval(() => {
this.choosed = this.turningList[index % 8]
index++
console.log(stepTime)









if (index >= 2 && index < 6) {
stepTime = stepTime + gap
window.clearInterval(timer)
setClock()
}else if (end - 4 <= index && index < end) {
stepTime = stepTime - gap
gap += -40
window.clearInterval(timer)
setClock()
} else if (index >= end) {
window.clearInterval(timer)
setTimeout(() => {
if (this.choosedItem.name === '谢谢惠顾') {
this.show = true
this.sorryText = '谢谢惠顾'
} else {
this.dialog = true
}
this.isDrawing = false
}, 1000)
return
}
}, stepTime)
}
setClock()
},
getWxConfig() {
Wechat.initHomeShare()
},
makeSnow () {
if (window.jQuery) {
this.timer = $.fn.snow({
query: '.header-lottery',
minSize: 10,		//雪花的最小尺寸
maxSize: 30, 	//雪花的最大尺寸
newOn: 1000		
})
}
},
clearSnow () {
if (window.jQuery) {
window.clearInterval(this.timer)
$('#snowbox').remove()
}
}
},
created () {
this.getWxConfig()
this.getLotteryCount()

this.getLuckyItems()
},
mounted () {
if (!local.get(local.KEY_USER_VISITED) &&
isDuringActivity &&
local.get(local.KEY_USER_TOKEN)) {
this.showSanta = true
}
},
computed: {
...mapState({
myInfo: state => state.user.myInfo,
mySource: state => state.user.mySource,
myCount: state => state.lottery.count,
santaOpen: state => state.user.santaOpen
})
},
beforeDestroy () {
this.clearSnow()
}
}
